import "./Style.scss";
// import Header from "./Header";
import React, { Component } from "react";
import { TextField, Grid, Button, OutlinedInput } from "@material-ui/core";
import { withRouter, Link } from "react-router-dom";
import axios from "axios";
class ResetPassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      password: "",
      passwordError: false,
      confirmPassword: "",
      confirmPasswordError: false,
      token: ""
    };
    this.checkoutSubmit = this.checkoutSubmit.bind(this);
  }
  async componentDidMount() {
    let temp = localStorage.getItem("resetToken");
    await axios
      .post(
        "https://jobs-api.squareboat.info/api/v1/auth/resetpassword/" + temp
      )
      .then((res) => {
        if (res.data.success != true) {
          alert("Invalid Token");
          this.props.history.push("/login");
        }
        this.setState({ token: temp });
      })
      .catch((error) => {
        this.props.history.push("/login");
      });
  }

  checkoutSubmit = async () => {
    let email = this.state.email;

    if (this.state.password === "") {
      this.setState({ passwordError: "Invalid Password" });
    } else {
      if (
        this.state.confirmPassword == "" ||
        this.state.confirmPassword !== this.state.password
      ) {
        this.setState({ confirmPasswordError: "Invalid Password" });
      } else {
        this.setState({ passwordError: "" });
      }
      const data1 = {
        password: this.state.password,
        confirmPassword: this.state.confirmPassword,
        token: this.state.token
      };
      axios
        .post(
          "https://jobs-api.squareboat.info/api/v1/auth/resetpassword",
          data1
        )
        .then((res) => {
          //console.log(res);
          //console.log(res.data);
          if (res.data.success === true) {
            localStorage.setItem("token", res.data.data.token);
            alert("Password Updated Successfully");
            this.props.history.push("/");
          } else {
            this.setState({ emailError: true, passwordError: true });
          }
        })
        .catch((error) => {
          if (error.response.data.message) {
            this.setState({ error: error.response.data.message });
          } else {
            let temp = Object.values(error.response.data.errors[0]);
            this.setState({ error: temp[0] });
          }
        });
    }
  };
  //Call the Login Api

  changeInputValue = (value, name) => {
    this.setState({
      [name]: value
    });
  };

  render() {
    return (
      <React.Fragment>
        <div className="login-container">
          <Grid item className="login-card" md={7} lg={5} xs={10} sm={9}>
            <h3>Reset Your Password</h3>
            <p style={{ color: "#303F60" }}>Enter your new password below</p>
            <h5>New Password</h5>

            <OutlinedInput
              required
              type="password"
              fullWidth
              onChange={(e) =>
                this.changeInputValue(e.target.value, "password")
              }
              helperText={
                this.state.passwordError ? this.state.passwordError : ""
              }
              error={this.state.passwordError ? true : false}
              labelWidth={0}
            />
            <h5>Confirm new password</h5>
            <OutlinedInput
              required
              fullWidth
              type="password"
              onChange={async (e) => {
                const data = await this.changeInputValue(
                  e.target.value,
                  "confirmPassword"
                );
                //console.log(this.state.password, this.state.confirmPassword);
                if (this.state.password == this.state.confirmPassword) {
                  this.setState({ confirmPasswordError: false });
                } else {
                  this.setState({ confirmPasswordError: true });
                }
              }}
              helperText={
                this.state.confirmPasswordError
                  ? this.state.confirmPasswordError
                  : ""
              }
              error={this.state.confirmPasswordError ? true : false}
              labelWidth={0}
            />
            <center>
              <p style={{ color: "#FF0000" }}>
                {this.state.error ? this.state.error : ""}
              </p>
              <Button
                variant="contained"
                color="primary"
                style={{ margin: "20px" }}
                onClick={this.checkoutSubmit}
              >
                Submit
              </Button>
              <p>
                New to MyJobs?
                <Link to="/signup">Create an account</Link>
              </p>
            </center>
          </Grid>
        </div>
      </React.Fragment>
    );
  }
}

export default withRouter(ResetPassword);
